.category-form-page{min-height:100vh;background:#f7f8fb}
.category-form-content{padding:88px 16px 96px;max-width:900px;margin:0 auto}
.category-card{background:#fff;border-radius:20px;box-shadow:0 20px 60px rgba(0,0,0,.08);overflow:hidden}
.category-header{display:flex;gap:16px;align-items:center;padding:18px 20px;background:linear-gradient(90deg,var(--cat-color,#667eea),#a78bfa)}
.cat-icon{font-size:32px;background:#fff;color:#111827;width:48px;height:48px;border-radius:12px;display:flex;align-items:center;justify-content:center;box-shadow:0 8px 20px rgba(0,0,0,.12)}
.cat-title{margin:0;color:#fff;font-size:22px;font-weight:800}
.cat-subtitle{margin:4px 0 0;color:#eef2ff;font-size:13px}
.fields-grid{display:grid;grid-template-columns:repeat(2,1fr);gap:16px;padding:18px}
.form-group{display:flex;flex-direction:column}
.form-group.full{grid-column:1/-1}
.label{font-size:12px;font-weight:700;color:#374151;margin-bottom:6px}
.input{border:1.5px solid #e5e7eb;border-radius:12px;padding:12px 14px;font-size:14px;transition:border-color .2s,box-shadow .2s}
.input:focus{outline:none;border-color:#667eea;box-shadow:0 0 0 3px rgba(102,126,234,.15)}
.input.error{border-color:#ef4444;background:#fff5f5}
.textarea{min-height:100px;resize:vertical}
.select{appearance:none;background-image:linear-gradient(45deg,transparent 50%,#9ca3af 50%),linear-gradient(135deg,#9ca3af 50%,transparent 50%);background-position:calc(100% - 16px) calc(1em + 2px),calc(100% - 11px) calc(1em + 2px);background-size:5px 5px,5px 5px;background-repeat:no-repeat}
.hint{font-size:11px;color:#6b7280;margin-top:4px}
.error-text{color:#ef4444}
.upload-area{border:2px dashed #e5e7eb;border-radius:12px;padding:14px;text-align:center;color:#6b7280}
.attachments{display:flex;flex-wrap:wrap;gap:8px;margin-top:8px}
.chip{display:flex;align-items:center;gap:8px;background:#f3f4f6;border:1px solid #e5e7eb;border-radius:9999px;padding:6px 10px;font-size:12px}
.chip button{background:#ef4444;border:none;color:#fff;border-radius:50%;width:18px;height:18px;line-height:18px;cursor:pointer}
.actions{display:flex;justify-content:flex-end;gap:10px;padding:0 18px 18px}
.btn{border:none;border-radius:12px;padding:12px 16px;font-weight:700;cursor:pointer}
.btn.primary{background:linear-gradient(135deg,#667eea,#764ba2);color:#fff}
.btn.secondary{background:#f3f4f6;color:#111827;border:1px solid #e5e7eb}

@media (max-width:768px){
  .fields-grid{grid-template-columns:1fr}
}


